.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: lightgray;
  font-weight: 600;
  text-transform: uppercase;

  &__top {
    margin-bottom: 24px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
    margin-bottom: 16px;
  }

  &_call-to-order {
    display: block;
    width: fit-content;
    color: $menu-text-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    padding-bottom: 7px;
    border-bottom: 1px solid black;
  }
}
